<!-- 我的 -->
<template>
  <div class="container">
    <van-nav-bar fixed title="我的" />
    <div class=" main-content">
      <div class="head-cont">
        <div class="title">我的账户</div>
        <div class="wallet" v-if="walletAddr">
          {{ isHide ? '******************' : walletAddr }}
          <img src="../assets/images/copy.png" @click="copy(walletAddr)" />
          <van-icon :name="isHide ? 'eye-o' : 'closed-eye'" size="20" @click="isHide = !isHide" />
        </div>
      </div>
      <van-cell-group>
        <van-cell icon="todo-list-o" title="投注记录" @click="pageJump('/recordList', true)" is-link :border="false" />
        <van-cell icon="vip-card-o" title="代理数据" @click="pageJump('/index', true)" is-link :border="false" />
        <van-cell icon="gift-card-o" title="个人福利" @click="pageJump('/benefits', true)" is-link :border="false" />
        <van-cell icon="records" title="投注记录" @click="pageJump('/recordList', true)" is-link :border="false" />
        <van-cell icon="cart-circle-o" title="购买虚拟货币" is-link :border="false" @click="hanldeOpenPopup('purchase')" />
        <van-cell icon="debit-pay" title="推荐VPN" is-link :border="false" @click="hanldeOpenPopup('download')" />
        <van-cell class="loginout" :class="{ 'diasble': !walletAddr }" title="退出" :border="false" @click="loginout" />
        <!-- <van-cell icon="envelop-o" title="电子邮箱" is-link :border="false" /> -->
      </van-cell-group>
    </div>
  </div>
  <Footer :index="3" />
  <van-popup v-model:show="showLayer" closeable position="bottom">
    <register v-show="layerType === 'register'" @open="handleOpen" />
    <purchase v-show="layerType === 'purchase'" />
    <download v-show="layerType === 'download'" />
    <prohibit v-show="layerType === 'prohibit'" @open="handleOpen" />
  </van-popup>
  <van-popup round v-model:show="showLogin">
    <login @login="login" />
  </van-popup>
</template>

<script>
import useClipboard from "vue-clipboard3";
import Register from "../components/Register";
import Download from "../components/Download";
import Purchase from "../components/Purchase";
import Prohibit from "../components/Prohibit"
import Login from "../components/Login";
import store from "../store";
import Footer from "../components/Footer";

export default {
  name: 'Mine',
  components: { Register, Download, Purchase, Login, Prohibit, Footer },
  data() {
    return {
      isHide: false,
      showLogin: false,
      showLayer: false,
      walletAddr: '',
      layerType: ''
    }
  },
  created() {
    const { toClipboard } = useClipboard();
    this.toClipboard = toClipboard;
    let { wallet_address } = store.getters;
    if (wallet_address) this.walletAddr = wallet_address;
  },
  methods: {
    copy(text) {
      this.toClipboard(text)
      this.$toast("复制成功!");
      console.log('Copied to clipboard')
    },
    hanldeOpenPopup(type) {
      this.layerType = type;
      this.showLayer = true;
    },
    handleOpen(type) {
      this.layerType = type === 1 ? 'register' : 'prohibit'
      this.showLayer = true;
    },
    pageJump(path, needLogin) {
      if (needLogin) {
        let { token } = store.getters;
        if (!token) return this.showLogin = true;
      }
      this.$router.push(path);
    },
    login(status) {
      console.log(status);
      this.showLogin = false;
      if (status) {
        this.walletAddr = store.getters.wallet_address
        // this.$router.push("/recordList");
      }
    },
    loginout() {
      if (!this.walletAddr) return;
      this.$dialog.confirm({
        title: '提示', message: "是否确认退出登录?",
        confirmButtonText: '退出'
      }).then(() => {
        store.commit("updateToken", "");
        store.commit("updateWalletAddress", "");
        this.$toast("退出成功");
        window.location.reload();
      });
    }
  }
}
</script>
<style lang='scss' scoped>
.van-nav-bar {
  background-color: var(--main-color);

  :deep(.van-nav-bar__title) {
    color: #fff;
  }

  :deep(.van-nav-bar__left) {
    .van-icon {
      color: #fff;
    }
  }

  &:after {
    border: none;
  }
}

.container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding-left: 0;
  padding-right: 0;
  box-sizing: border-box;
  background-color: #f3f6fe;

  .head-cont {
    color: #fff;
    padding: 10px;
    background-color: var(--main-color);

    .title {
      font-size: 16px;
    }

    .wallet {
      color: #b8d0fe;
      padding-top: 5px;
      display: flex;
      align-items: center;

      img {
        width: 20px;
        padding: 0 5px;
      }
    }
  }

  .van-cell-group {
    background: none;
    padding: 20px 10px 10px;

    .van-cell {
      border-radius: 8px;
      margin-bottom: 10px;

      :deep(.van-cell__left-icon) {
        color: var(--main-color);
        font-size: 20px;
        margin-right: 10px;
      }

      :deep(.van-cell__title) {
        color: #333;
      }

      &.login-out {
        :deep(.van-cell__title) {
          color: #999;
          text-align: center;
        }
      }
    }

    .loginout {
      text-align: center;

      &.diasble {
        :deep(.van-cell__title) {
          color: #999;
        }
      }
    }
  }
}
</style>